/**
 * 在 form 表单中有标题的 Input 组件
 */
import { InputNumber } from "antd";
import React, { useState } from "react";
import { ValidateComp } from "./ValidateComp";
import { Note } from "./NoteComp";

/**
 * FormInput 必须的 props
 */
export interface FormInputNumberpProps {
  /** 自动获取焦点 */
  autoFocus?: boolean;
  /** 固定校验规则 */
  checkFixedRule?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
  /** 小数点 */
  decimalSeparator?: string;
  /** 禁用 */
  disabled?: boolean;
  /** 自定义的多选框清空图标 */
  formatter?: (value?: number | string) => string;
  /** 最大值 */
  max?: number;
  /** 最小值 */
  min?: number;
  /** 指定从 formatter 里转换回数字的方式，和 formatter 搭配使用 */
  parser?: (param: string | undefined) => number | string;
  /** 小数位数 */
  precision?: number;
  /** 只读 */
  readOnly?: boolean;
  /** 尺寸 */
  size?: "large" | "middle" | "small";
  /** 每次改变的步数 */
  step?: number;
  /** 标题 */
  title?: string;
  /** 标题颜色 */
  labelColor?: string;
  /** 显示值 */
  showVal?: number;
  /** 提示信息 */
  placeholder?: string;
  /** 备注信息 */
  note?: string;
  /** 校验失败的备注信息 */
  noteForError?: string;
  /** 实际值 */
  realVal?: number;
  /** 值改变 */
  onChange?: (param: { realVal: number | undefined | null }) => void;
  /** 失焦 */
  onBlur?: (param: {
    event: React.FocusEvent;
    realVal?: number;
    showVal?: number;
  }) => void;
  /** 聚焦 */
  onFocus?: (param: {
    event: React.FocusEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /** 单击 */
  onClick?: (param: {
    event: React.MouseEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /** 双击 */
  onDbClick?: (param: {
    event: React.MouseEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /** 按下回车的回调 */
  onPressEnter?: (param: {
    event: React.KeyboardEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /** 点击上下箭头的回调 */
  onStep?: (param: {
    realVal: number;
    info: { offset: number; type: "up" | "down" };
  }) => void;
}

export const FormInputNumber: React.FC<FormInputNumberpProps> = (props) => {
  const {
    autoFocus = false,
    checkFixedRule,
    decimalSeparator,
    disabled = false,
    formatter,
    min,
    max,
    parser,
    precision,
    readOnly = false,
    size = "middle",
    step,
    title,
    labelColor,
    note,
    placeholder,
    realVal,
    showVal,
    onChange,
    onBlur,
    onFocus,
    onClick,
    onDbClick,
    onStep,
  } = props;
  const [isFocus, setState] = useState(false);
  return (
    <div>
      {title ? (
        <div
          className="leading-10"
          style={{
            color: labelColor,
          }}
        >
          {title}
          <Note title={note} />
        </div>
      ) : null}

      <ValidateComp
        value={realVal}
        type={checkFixedRule}
        onBlur={(event) => {
          setState(false);
          onBlur?.({ realVal, showVal, event });
        }}
      >
        <InputNumber
          style={{ width: 300 }}
          disabled={disabled}
          autoFocus={autoFocus}
          decimalSeparator={decimalSeparator}
          formatter={formatter}
          min={min}
          max={max}
          parser={parser}
          precision={precision}
          readOnly={readOnly}
          size={size}
          step={step}
          placeholder={placeholder}
          value={realVal}
          onChange={(realValue) => {
            if (typeof onChange !== "function") return;
            onChange({
              realVal:
                typeof realValue === "string"
                  ? parseFloat(realValue)
                  : realValue,
            });
          }}
          onFocus={(event) => {
            setState(true);
            if (typeof onFocus !== "function") return;
            onFocus({ realVal, showVal, event });
          }}
          onClick={(event) => {
            if (typeof onClick !== "function") return;
            onClick({ realVal, showVal, event });
          }}
          onDoubleClick={(event) => {
            if (typeof onDbClick !== "function") return;
            onDbClick({ realVal, showVal, event });
          }}
          onStep={(realValue, info) => {
            if (typeof onStep !== "function") return;
            onStep({ realVal: realValue, info });
          }}
        />
      </ValidateComp>
    </div>
  );
};
