/**
 * 在 form 表单中有标题的 Input 组件
 */
import React, { useState } from "react";
import { Input } from "antd";
import { ValidateComp } from "./ValidateComp";
import { Note } from "./NoteComp";

/**
 * FormInput 必须的 props
 */
export interface TextareaCompProps {
  /** 是否允许清楚数据 */
  allowClear?: boolean;
  /** 自适应高度 */
  autoSize?: boolean | { minRows: number; maxRows: number };
  /** 显示边框 */
  bordered?: boolean;
  /** 固定校验规则 */
  checkFixedRule?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
  /** 标题颜色 */
  labelColor?: string;
  /** 计数的格式化 */
  // countFormatter?: (count: number, maxLength?: number) => string;
  /** 最大长度 */
  maxLength?: number;
  /** 备注信息 */
  note?: string;
  /** 校验失败的提示信息 */
  noteForError?: string;
  /** 提示信息 */
  placeholder?: string;
  /** 行数 */
  rows?: number;
  /** 实际值 */
  realVal?: string;
  /** 显示计数 */
  showCount?: boolean;
  /** 显示值 */
  showVal?: string;
  /** 标题 */
  title?: string;
  /** 值改变 */
  onChange?: (param: { realVal: string; event: React.ChangeEvent }) => void;
  /** 失焦 */
  onBlur?: (param: {
    event: React.FocusEvent;
    realVal: string | undefined;
    showVal: string | undefined;
  }) => void;
  /** 聚焦 */
  onFocus?: (param: {
    event: React.FocusEvent;
    realVal: string | undefined;
    showVal: string | undefined;
  }) => void;
  /** 单击 */
  onClick?: (param: {
    event: React.MouseEvent;
    realVal: string | undefined;
    showVal: string | undefined;
  }) => void;
  /** 双击 */
  onDbClick?: (param: {
    event: React.MouseEvent;
    realVal: string | undefined;
    showVal: string | undefined;
  }) => void;
  /** 按下回车的回调 */
  onPressEnter?: (param: {
    event: React.KeyboardEvent;
    realVal: string | undefined;
    showVal: string | undefined;
  }) => void;
}

export const Textarea: React.FC<TextareaCompProps> = (props) => {
  const {
    allowClear = false,
    autoSize,
    bordered,
    checkFixedRule,
    labelColor = "#000",
    maxLength,
    note,
    placeholder,
    realVal,
    rows,
    showCount = false,
    showVal,
    title,
    // countFormatter,
    onChange,
    onBlur,
    onFocus,
    onDbClick,
    onClick,
    onPressEnter,
  } = props;
  const [isFocus, setState] = useState(false);
  return (
    <div>
      {title ? (
        <div
          className="leading-10"
          style={{
            color: labelColor,
          }}
        >
          {title}
          <Note title={note} />
        </div>
      ) : null}
      <ValidateComp
        value={realVal}
        type={checkFixedRule}
        onBlur={(event) => {
          setState(false);
          onBlur?.({ realVal, showVal, event });
        }}
      >
        <Input.TextArea
          allowClear={allowClear}
          autoSize={autoSize || false}
          bordered={"bordered" in props ? bordered : true}
          maxLength={maxLength}
          placeholder={placeholder}
          rows={rows}
          showCount={showCount}
          // countFormatter={(count, maxLength) => {
          //   if (countFormatter) {
          //     return countFormatter(count, maxLength);
          //   }
          //   return count + (maxLength ? ` / ${maxLength}` : "");
          // }}
          value={isFocus || !showVal ? realVal : showVal}
          onChange={(event) => {
            if (typeof onChange !== "function") return;
            onChange({ realVal: event.target.value, event });
          }}
          onFocus={(event) => {
            setState(true);
            if (typeof onFocus !== "function") return;
            onFocus({ realVal, showVal, event });
          }}
          onClick={(event) => {
            if (typeof onClick !== "function") return;
            onClick({ realVal, showVal, event });
          }}
          onDoubleClick={(event) => {
            if (typeof onDbClick !== "function") return;
            onDbClick({ realVal, showVal, event });
          }}
          onPressEnter={(event) => {
            if (typeof onPressEnter !== "function") return;
            onPressEnter({ realVal, showVal, event });
          }}
        />
      </ValidateComp>
    </div>
  );
};
