/**
 * 在 form 表单中有标题的 Input 组件
 */
import React, { useState } from "react";
import { Input } from "antd";
import { ValidateComp } from "./ValidateComp";
import { Note } from "./NoteComp";
/**
 * FormInput 必须的 props
 */
export interface FormInputProps {
  /** 自动获取焦点 */
  autoFocus?: boolean;
  /** 后置标签 */
  addonAfter?: React.ReactNode;
  /** 前置标签 */
  addonBefore?: React.ReactNode;
  /** 是否允许清除内容 */
  allowClear?: boolean;
  /** 是否要边框 */
  bordered?: boolean;
  /** 固定校验规则 */
  checkFixedRule?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
  /** 禁用 */
  disabled?: boolean;
  /** 标题颜色 */
  labelColor?: string;
  /** 备注信息 */
  note?: string;
  /** 校验失败的提示信息 */
  noteForError?: string;
  /** 提示信息 */
  placeholder?: string;
  /** 前缀图标 */
  prefix?: React.ReactNode;
  /** 实际值 */
  realVal?: string;
  /** 显示值 */
  showVal?: string;
  /** 尺寸 large: 40px/middle: 32px/small: 24px */
  size?: "large" | "middle" | "small";
  /** 后缀 */
  suffix?: React.ReactNode;
  /** 标题 */
  title?: string;
  /** 值改变 */
  onChange?: (param: { realVal: string; event: React.ChangeEvent }) => void;
  /** 失焦 */
  onBlur?: (param: {
    event: React.FocusEvent;
    realVal: string;
    showVal: string;
  }) => void;
  /** 聚焦 */
  onFocus?: (param: {
    event: React.FocusEvent;
    realVal: string;
    showVal: string;
  }) => void;
  /** 单击 */
  onClick?: (param: {
    event: React.MouseEvent;
    realVal: string;
    showVal: string;
  }) => void;
  /** 双击 */
  onDbClick?: (param: {
    event: React.MouseEvent;
    realVal: string;
    showVal: string;
  }) => void;
  /** 按下回车的回调 */
  onPressEnter?: (param: {
    event: React.KeyboardEvent;
    realVal: string;
    showVal: string;
  }) => void;
}

export const FormInput: React.FC<FormInputProps> = (props) => {
  const {
    autoFocus = false,
    addonAfter,
    addonBefore,
    allowClear = false,
    bordered,
    disabled = false,
    labelColor = "#A4A4A4",
    note,
    placeholder,
    prefix,
    realVal = "",
    showVal = "",
    size = "middle",
    title,
    checkFixedRule,
    onChange,
    onBlur,
    onFocus,
    onDbClick,
    onClick,
    onPressEnter,
  } = props;
  const [isFocus, setState] = useState(false);
  const [internalValue, setInternalValue] = useState("");
  return (
    <div>
      <div
        className="leading-10"
        style={{
          color: labelColor,
        }}
      >
        {title}
        <Note title={note} />
      </div>
      <ValidateComp
        value={realVal}
        type={checkFixedRule}
        onBlur={(event) => {
          setState(false);
          onBlur?.({ realVal, showVal, event });
        }}
      >
        <Input
          autoFocus={autoFocus}
          addonBefore={addonBefore}
          addonAfter={addonAfter}
          allowClear={allowClear}
          bordered={"bordered" in props ? bordered : true}
          disabled={disabled}
          placeholder={placeholder}
          prefix={prefix}
          size={size}
          value={isFocus || !showVal ? realVal : showVal}
          onChange={(event) => {
            setInternalValue(event.target.value);
            if (typeof onChange !== "function") return;
            onChange({ realVal: event.target.value, event });
          }}
          onFocus={(event) => {
            setState(true);
            if (typeof onFocus !== "function") return;
            onFocus({ realVal, showVal, event });
          }}
          onClick={(event) => {
            if (typeof onClick !== "function") return;
            onClick({ realVal, showVal, event });
          }}
          onDoubleClick={(event) => {
            if (typeof onDbClick !== "function") return;
            onDbClick({ realVal, showVal, event });
          }}
          onPressEnter={(event) => {
            if (typeof onPressEnter !== "function") return;
            onPressEnter({ realVal, showVal, event });
          }}
        />
      </ValidateComp>
    </div>
  );
};
