import React from "react";
import { DataSelectorAsTable } from "./DataSelectorAsTable";
export interface FormButtonProps {
  /** 将按钮宽度调整为其父宽度的选项 */
  block?: boolean;
  /** 失效 */
  disabled?: boolean;
  /** 背景透明 */
  ghost?: boolean;
  /** 图标 */
  icon?: React.ReactNode;
  /** 按钮形状 */
  shape?: "circle" | "round";
  /** 尺寸： */
  size?: "small" | "large" | "middle";
  /** 按钮类型 */
  type?: "primary" | "ghost" | "dashed" | "link" | "text" | "default";
  /** 按钮内部的显示内容 */
  title: React.ReactNode;
  /** 备注信息 */
  note?: string;
  /** 单击 */
  onClick?: (param: { event: React.MouseEvent }) => void;
  /** 双击 */
  onDbClick?: (param: { event: Event }) => void;
  /** 鼠标移入 */
  onMouseIn?: (param: { event: Event }) => void;
  /** 鼠标移出 */
  onMouseOut?: (param: { event: Event }) => void;
}
export interface ModalProps {
  /** Modal 完全关闭后的回调 */
  afterClose?: () => void;
  /** Modal body 样式 */
  bodyStyle?: CSSProperties;
  /** cancel 按钮 props */
  cancelButtonProps?: FormButtonProps;
  /** 取消按钮文字 */
  cancelText?: React.ReactNode;
  /** 垂直居中展示 Modal */
  centered?: boolean;
  /** 是否显示右上角的关闭按钮 */
  closable?: boolean;
  /** 自定义关闭图标 */
  closeIcon?: React.ReactNode;
  /** 确定按钮 loading */
  confirmLoading?: boolean;
  /** 关闭时销毁 Modal 里的子元素 */
  destroyOnClose?: boolean;
  /** 对话框关闭后是否需要聚焦触发元素 */
  focusTriggerAfterClose?: boolean;
  /** 底部内容 */
  footer?: React.ReactNode;
  /** 强制渲染 Modal */
  forceRender?: boolean;
  /** 指定 Modal 挂载的 HTML 节点, false 为挂载在当前 dom */
  getContainer?: HTMLElement | (() => HTMLElement) | false;
  /** 是否支持键盘 esc 关闭 */
  keyboard?: boolean;
  /** 是否展示遮罩 */
  mask?: boolean;
  /** 点击蒙层是否允许关闭 */
  maskClosable?: boolean;
  /** 遮罩样式 */
  maskStyle?: CSSProperties;
  /** 自定义渲染对话框 */
  modalRender?: (node: React.ReactNode) => React.ReactNode;
  /** ok 按钮 props */
  okButtonProps?: FormButtonProps;
  /** 确认按钮文字 */
  okText?: React.ReactNode;
  /** 确认按钮类型 */
  okType?: string;
  /** 可用于设置浮层的样式，调整浮层位置等 */
  style?: CSSProperties;
  /** 标题 */
  title?: string;
  /** 对话框是否可见 */
  visible?: boolean;
  /** 宽度 */
  width?: string | number;
  /** 对话框外层容器的类名 */
  wrapClassName?: string;
  /** 设置 Modal 的 z-index */
  zIndex?: number;
  /** 子元素 */
  children: React.ReactNode;
  onCancel?: () => void;
  onOk?: (value) => void;
}
export interface TableProps {
  dataSource: { [key: string]: unknown }[];
  rowKey: string;
  columns: { dataIndex: string; title: string }[];
  onRequest: (
    pagination: { current: number; pageSize: number },
    filter: { [key: string]: unknown }
  ) => void;
  tagColumn: string;
  rowSelection: {
    type: "radio" | "checkbox";
    count?: number;
  };
  selectedRows?: { [key: string]: unknown }[];
  pagination?: {
    total?: number;
    current?: number;
    pageSize?: number;
  };
}
export type props = {
  modal: ModalProps;
  source: { type: "table" } & TableProps;
};
export const DataSelector = (props) => {
  const { modal, source } = props;
  const { type, ...rest } = source;
  if (type === "table") {
    return <DataSelectorAsTable modal={modal} table={rest} />;
  }
  return null;
};
