/**
 * 弹窗
 */
import { Modal } from "antd";
import React, { Component } from "react";

class HYPageModal extends Component<any, any> {
  constructor(props) {
    super(props);
    console.log(props);
    this.state = {
      visible: false,
    };
  }

  render() {
    const CustomTag = eval(`${this.props.pageComponent}`);
    /*
    let key = `${this.props.pageComponent}`;
    if (this.props.pageDTO !== undefined && this.props.pageDTO.id !== undefined) {
      key = `${this.props.pageComponent}_${this.props.pageDTO.id}`;
    }
    */
    return (
      <>
        <Modal
          visible={this.props.visible}
          footer={null}
          onCancel={this.props.onCancel}
          title={this.props.title}
          destroyOnClose={true}
          bodyStyle={{ maxHeight: "70vh", overflow: "auto" }}
          maskClosable={false}
        >
          <CustomTag
            isFromModal={true}
            {...this.props.pageDTO}
            onOk={this.props.onOk}
            onCancel={this.props.onCancel}
          ></CustomTag>
        </Modal>
      </>
    );
  }
}

export { HYPageModal };
