/**
 * 在 form 表单中有标题的 Input 组件
 */
import React from "react";
import { Button } from "antd";

/**
 * FormInput 必须的 props
 */
export interface FormButtonProps {
  /** 将按钮宽度调整为其父宽度的选项 */
  block?: boolean;
  /** 失效 */
  disabled?: boolean;
  /** 背景透明 */
  ghost?: boolean;
  /** 图标 */
  icon?: React.ReactNode;
  /** 按钮形状 */
  shape?: "circle" | "round";
  /** 尺寸： */
  size?: "small" | "large" | "middle";
  /** 按钮类型 */
  type?: "primary" | "ghost" | "dashed" | "link" | "text" | "default";
  /** 按钮内部的显示内容 */
  title: React.ReactNode;
  /** 备注信息 */
  note?: string;
  /** 单击 */
  onClick?: (param: { event: React.MouseEvent }) => void;
  /** 双击 */
  onDbClick?: (param: { event: Event }) => void;
  /** 鼠标移入 */
  onMouseIn?: (param: { event: Event }) => void;
  /** 鼠标移出 */
  onMouseOut?: (param: { event: Event }) => void;
}

export const FormButton: React.FC<FormButtonProps> = (props) => {
  const {
    block = false,
    disabled = false,
    ghost = false,
    icon,
    shape,
    size = "middle",
    type = "primary",
    title = "按钮",
    onClick,
  } = props;

  return (
    <Button
      block={block}
      disabled={disabled}
      ghost={ghost}
      icon={icon}
      shape={shape}
      size={size}
      type={type}
      onClick={(event) => {
        onClick?.({ event });
      }}
    >
      {title}
    </Button>
  );
};
