import React, { useState } from "react";
import { BlurAway } from "./BlurAway";
// import { EXP } from "@hyc/hyclib-web-client";
import { CloseCircleOutlined } from "@ant-design/icons";
export type ValidateProps = {
  children?: React.ReactNode;
  onBlur?: (e: React.FocusEvent) => void;
  type?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
};
const validMsg = {
  naturalNumber: "请输入自然数",
  number: "请输入数字",
  english: "请输入英文",
  chinese: "请输入中文",
  integer: "请输入整数",
  numberOrEnglish: "请输入数字或英文",
  cellphone: "请输入正确的手机号码",
  email: "请输入正确的电子邮箱",
  idCard: "请输入正确的身份证号码",
  postalCode: "请输入邮政编码",
  ip: "请输入ip地址",
};
export const ValidateComp = (props) => {
  const [flag, setFlag] = useState(false);
  const validValue = () => {
    const { value, type } = props;
    if (!type || EXP.validateEXP(value, type)) {
      setFlag(false);
    } else {
      setFlag(true);
    }
  };
  return (
    <BlurAway
      onBlur={(e) => {
        validValue();
        props.onBlur?.(e);
      }}
    >
      {props.children}
      {flag ? (
        <>
          <CloseCircleOutlined
            style={{
              color: "#F1252D",
            }}
            className="mx-2"
          />
          <span className="text-red">{validMsg[props.type]}</span>
        </>
      ) : null}
    </BlurAway>
  );
};
