import { LightFilter, ProFormDatePicker } from "@ant-design/pro-form";
// import ProTable from "@ant-design/pro-table";
import { Button, ConfigProvider, Table } from "antd";
import zhCN from "antd/es/locale/zh_CN";
import React from "react";
import { Note } from "./NoteComp";

export type QueryStyleAsForm = { queryStyle: "asForm" };
export type QueryStyleInToolbar = { queryStyle: "inToolbar" };
export type QueryTypeTypical = {
  typical: ({ maxNum?: number } & QueryStyleInToolbar) | QueryStyleAsForm;
};
export type QueryTypeSpecial = {
  special: QueryStyleInToolbar | QueryStyleAsForm;
};
export type Column = {
  dataIndex: string;
  title: string;
  show?: boolean;
};
export type Btn = {
  type: "create" | "update" | "detail" | "delete" | "query";
  title: string;
};
export interface GeneralTableCompProps {
  /** 列配置 */
  columns?: Column[];
  /** 数据列表 */
  dataSource?: { [key: string]: unknown }[];
  /** 默认展示第几页 */
  defaultPageSize?: number;
  /** 表头按钮 */
  headerBtns?: [];
  headerBtnsRenderer: () => React.ReactNode;
  /** 行内按钮 */
  inlineBtnsRenderer: () => React.ReactNode;
  /** 备注信息 */
  note?: string;
  /** 分页设置 */
  pagination?: false | { pageSize: number; total: number; current: number };
  /** 表格当前页数 */
  current?: number;
  /** 表格每页显示行数 */
  pageSize?: number;
  /** 数据总数 */
  total?: number;
  /** 查询方式，普通查询/高级查询/关键字查询 */
  queryType?: QueryTypeTypical & QueryTypeSpecial & { keyword: null };
  /** 是否选中数据行：不选中/单选/多选；选中形式：选择框/高亮行/高亮行+选择框 */
  rowSelection?: {
    type?: "no" | "radio" | "checkbox";
    checkedStyle?: "activeRow" | "checkedCellNActiveRow";
  };
  /** 行数据唯一标识 */
  rowKey?: string;
  /** 选中行唯一标识列表 */
  selectedRowKeys?: string[];
  /** 选中行列表 */
  selectedRows: { [key: string]: unknown }[];
  /** 是否显示序号列 */
  showOrderColumn?: boolean;
  /** 展示尺寸 */
  size?: "large" | "middle" | "small";
  /** 标题 */
  title?: string;
  /** 标题对齐方式 */
  titleAlign?: "left" | "right" | "center";
  /** 是否换行 */
  wordWrap?: boolean;
  onQuery: (param: {
    pagination?:
      | { current?: number; pageSize?: number; total?: number }
      | boolean;
  }) => void;
  onRowClick: (param: {
    record: Record<string, unknown>;
    event: React.MouseEvent;
  }) => void;
  onRowDbClick: (param: {
    record: Record<string, unknown>;
    event: React.MouseEvent;
  }) => void;
  onRowSelect: (param: {
    event?;
    selectedRows: { [key: string]: unknown }[];
    selectedRowKeys: string[];
    record?: { [key: string]: unknown };
  }) => void;
}

export class GeneralTableComp extends React.Component<GeneralTableCompProps> {
  state = {};

  componentDidMount = () => {
    const { pagination } = this.props;
    this.props.onQuery?.({
      pagination,
    });
  };
  /** 表格内部定制按钮（增删改查）点击事件 */
  btnClickTrigger = ({ event, triggerIndex, type }) => {
    const getTriggerHandler = () => {
      const triggerHandlerMap = {
        create: "onAdd",
        update: "onEdit",
        detail: "onDetail",
        delete: "onRemove",
      };
      return triggerHandlerMap[type];
    };

    const triggerHandler = getTriggerHandler();
    this.props[triggerHandler]?.({
      ...this.props,
      triggerIndex,
      event,
    });
  };

  /** 拼接展示列 */
  getColumns = ({ columns, wordWrap, showOrderColumn, inlineBtnsRenderer }) => {
    /** 根据表格列的“显示”属性，过滤展示列 */
    const filterColumnsWithShowProp = (columnsParam) => {
      return (
        columnsParam?.filter((column) => {
          return "show" in column ? column.show : true;
        }) || []
      );
    };
    /** 单元格内是否支持换行 */
    const getEllipsis = () => {
      if (wordWrap) return {};
      return { ellipsis: true };
    };
    /** 单元格样式类 */
    const getClassName = () => {
      /** 单元格换行 */
      const cNForEllipsis = () => {
        if (wordWrap) return "break-all";
      };
      return [cNForEllipsis()].join(" ");
    };
    /** 获取序号列 */
    const getOrderColumn = (showOrder) => {
      if (showOrder) {
        return [
          {
            align: "center",
            dataIndex: "$$order",
            fixed: "left",
            key: "key",
            title: "序号",
            search: false,
            render: (_t, _r, _i) => _i + 1,
            width: 80,
          },
        ];
      }
      return [];
    };
    /** 行内按钮的渲染 */
    const getInlineAction = () => {
      if (inlineBtnsRenderer) {
        const _this = this;
        return [
          {
            dataIndex: "$$action",
            key: "$$action",
            title: "操作",
            align: "left",
            fixed: "right",
            // width: 60 * inlineBtnsParam.length,
            search: false,
            render: (_t, record, index) =>
              inlineBtnsRenderer({ record, index, rowClick: _this.rowClick }),
          },
        ];
      }
      return [];
    };
    /** 过滤显示列 */
    const showColumns = filterColumnsWithShowProp(columns);
    /** 换行 */
    const ellipsis = getEllipsis();
    /** 样式类（换行） */
    const className = getClassName();
    /** 序号列 */
    const orderColumn = getOrderColumn(showOrderColumn);
    /** 行内按钮列 */
    const actionColumn = getInlineAction();
    /** 没有数据列的情况下，显示展示列，有点奇怪 */
    if (showColumns.length === 0) {
      return [];
    }
    const result = [...orderColumn, ...showColumns, ...actionColumn].map(
      (item) => ({
        className,
        ...ellipsis,
        ...item,
      })
    );
    return result;
  };

  /** 根据查询方式，决定标题和搜索框的位置 */
  getPropsForSearch = ({ queryType, title, columns }) => {
    const getSearch = () => {
      const { typical } = queryType || {};
      const { queryStyle } = typical || {};
      return queryStyle === "asForm" ? {} : { search: false };
    };
    const getToolbarOrTitle = () => {
      const { typical } = queryType || {};
      const { queryStyle, maxNum } = typical || {};
      if (queryStyle !== "asForm" && (columns || []).length > 0) {
        return {
          toolbar: {
            title,
            filter: (
              <LightFilter>
                {(columns || []).slice(0, maxNum).map((item) => (
                  <ProFormDatePicker label={item.title} key={item.dataIndex} />
                ))}
              </LightFilter>
            ),
          },
        };
      }
      return { headerTitle: title };
    };
    // TODO 由于应用端暂不支持搜索，所以先不放开搜索框
    return { ...getToolbarOrTitle(), ...getSearch(), search: false };
  };

  /** 获取表格选中方式 */
  getRowSelection = ({
    dataSource,
    rowSelection,
    selectedRowKeys,
    selectedRows,
    rowKey,
  }) => {
    const { onRowSelect } = this.props;
    const { type, checkedStyle } = rowSelection || {};
    if (
      !(dataSource?.length > 0) ||
      checkedStyle === "activeRow" ||
      !type ||
      type === "no"
    ) {
      return {};
    }
    selectedRows = selectedRows || [];
    selectedRowKeys =
      Array.isArray(selectedRowKeys) && selectedRowKeys.length > 0
        ? selectedRowKeys
        : selectedRows.map((item) => item?.[rowKey]);
    return {
      rowSelection: {
        type,
        selectedRowKeys,
        onSelect: (record, selected, _s, event) => {
          this.handleRowSelect({ record, selected, event });
          event.stopPropagation();
        },
        onSelectAll: (selected, _s, changeRows) => {
          const changeRowKeysTmpl = changeRows.map((item) => item?.[rowKey]);
          if (selected) {
            onRowSelect?.({
              selectedRows: [...changeRows, ...selectedRows],
              selectedRowKeys: [...changeRowKeysTmpl, ...selectedRowKeys],
            });
          } else {
            onRowSelect?.({
              selectedRows: selectedRows.filter(
                (item) => !changeRowKeysTmpl.includes(item?.[rowKey])
              ),
              selectedRowKeys: selectedRowKeys.filter(
                (item) => !changeRowKeysTmpl.includes(item)
              ),
            });
          }
        },
      },
    };
    // TODO 应用端的交互效果
  };

  handleRowSelect = ({ record, selected, event }) => {
    const { rowKey = "id", onRowSelect, rowSelection } = this.props;
    const { type } = rowSelection || {};
    let { selectedRows } = this.props;
    selectedRows = selectedRows || [];
    let selectedRowsTmp = selected
      ? [record, ...selectedRows]
      : selectedRows.filter((item) => item[rowKey] !== record[rowKey]);
    let selectedRowKeysTmp = selectedRowsTmp.map((item) => item?.[rowKey]);
    if (type === "radio") {
      selectedRowsTmp = selectedRowsTmp.slice(0, 1);
      selectedRowKeysTmp = selectedRowKeysTmp.slice(0, 1);
    }
    onRowSelect?.({
      record,
      selectedRows: selectedRowsTmp,
      selectedRowKeys: selectedRowKeysTmp,
      event,
    });
  };

  handleRowClickToRowSelect = ({ event, record, selected }) => {
    const { rowSelection, rowKey = "id", selectedRowKeys } = this.props;
    const { type } = rowSelection || {};
    if (["no", undefined].includes(type)) return;
    this.handleRowSelect({
      record,
      event,
      selected:
        selected === undefined
          ? !selectedRowKeys?.includes(record?.[rowKey])
          : selected,
    });
  };

  /** 获取标题行样式类 */
  getTableTitleClassName = ({
    titleAlign,
    queryType,
    headerBtnsRenderer,
    headerBtns,
  }) => {
    /** 判断查询方式是否是在标题位置 */
    const hasInToolbar = Object.values(queryType || []).some(
      (item) => item?.queryStyle === "inToolbar"
    );
    /** 判断是否有表头按钮 */
    const hasHeaderBtns = !!headerBtnsRenderer || headerBtns?.length > 0;
    /** 标题位置：只有标题栏没有其他东西时，对齐方式才能起作用 */
    return hasInToolbar || hasHeaderBtns ? "" : `w-full text-${titleAlign}`;
  };

  /** 获取表格样式类 */
  getTableClassName = () => {
    return "";
  };

  /** 获取行渲染的列名 */
  getRowClassName = (_r, _i) => {
    const { selectedRowKeys, rowSelection, rowKey = "id" } = this.props;
    const { type, checkedStyle } = rowSelection || {};
    if (!selectedRowKeys || !(selectedRowKeys?.length > 0)) return "";
    if (!type || type === "no" || !checkedStyle) {
      return "";
    }
    if (selectedRowKeys?.includes(_r[rowKey])) return "ant-table-row-selected";
    return "";
  };

  /** 分页 */
  getPagination = ({ pagination, dataSource, pageSize, current, total }) => {
    if (!pagination) {
      return false;
    }
    return {
      pageSizeOptions: ["10", "20", "30", "40", "50", "100"],
      showQuickJumper: true,
      showSizeChanger: true,
      total: total || dataSource?.length || 0,
      size: "small",
      showTotal: (total, range) => {
        return `总共 ${total} 条`;
      },
      ...pagination,
      pageSize,
      current,
    };
  };

  rowClick = async ({ event, record, selected }) => {
    await this.props.onRowClick?.({ event, record });
    await this.handleRowClickToRowSelect({ event, record, selected });
  };

  render = () => {
    const {
      /** 配置的列 */
      columns = [],
      /** 配置的数据源 */
      dataSource = [],
      /** 表头按钮 */
      headerBtns,
      headerBtnsRenderer,
      /** 表格行内按钮 */
      inlineBtnsRenderer,
      /** 备注信息 */
      note,
      /** 分页配置 */
      pagination,
      current,
      pageSize,
      total,
      /** 查询方式 */
      queryType,
      /** 选中方式 */
      rowSelection,
      /** 唯一标识 */
      rowKey = "id",
      selectedRowKeys = [],
      selectedRows = [],
      /** 是否先是序号列 */
      showOrderColumn,
      /** 展示尺寸 */
      size = "small",
      /** 表格标题 */
      title,
      /** 表格标题位置 */
      titleAlign,
      /** 单元格换行 */
      wordWrap,
      /** 触发查询 */
      onQuery,
      /** 行点击 */
      onRowClick,
      /** 行双击 */
      onRowDbClick,
      ...other
    } = this.props || {};

    const decoratedRowSelection = this.getRowSelection({
      dataSource,
      rowSelection,
      selectedRowKeys,
      selectedRows,
      rowKey,
    });
    const decoratedColumns = this.getColumns({
      columns: Array.isArray(columns) ? columns : [],
      showOrderColumn,
      wordWrap,
      inlineBtnsRenderer,
    });
    // const decorativeProps = this.getPropsForSearch({
    //   title,
    //   queryType,
    //   columns,
    // });
    /** 标题样式类 */
    const titleClassName = this.getTableTitleClassName({
      headerBtns,
      headerBtnsRenderer,
      titleAlign,
      queryType,
    });
    /** 表格样式类 */
    const className = this.getTableClassName();
    /** 分页配置 */
    const decoratedPagination = this.getPagination({
      pagination,
      dataSource,
      current,
      pageSize,
      total,
    });
    return (
      <div className="general-table p-2">
        <div className="py-2 flex general-table-header">
          <div className={`title text-lg ${titleClassName}`}>
            {title}
            <Note title={note} />
          </div>
          <div className="flex"></div>
          <div className="header-btn">
            <Button
              onClick={() => {
                this.props.onQuery({
                  pagination: { ...decoratedPagination, current: 1 },
                });
              }}
              type="primary"
            >
              查询
            </Button>
            {headerBtns?.map((btn) => (
              <Button
                className="ml-2"
                key={btn.id}
                type="primary"
                size="middle"
              >
                {btn.title}
              </Button>
            ))}
            {headerBtnsRenderer && headerBtnsRenderer()}
          </div>
        </div>
        <ConfigProvider locale={zhCN}>
          <Table
            size={size}
            className={className}
            columns={decoratedColumns || []}
            dataSource={Array.isArray(dataSource) ? dataSource : []}
            pagination={decoratedPagination}
            rowClassName={this.getRowClassName}
            rowKey={rowKey}
            scroll={{ y: 400 }}
            {...decoratedRowSelection}
            // {...decorativeProps}
            {...other}
            onChange={(
              pageParam,
              filters,
              sorter,
              extra: { currentDataSource; action }
            ) => {
              onQuery?.({ pagination: pageParam });
            }}
            onRow={(record) => {
              return {
                onClick: (event) => {
                  this.rowClick({ event, record });
                },
                onDoubleClick: async (event) => {
                  await onRowDbClick?.({ event, record });
                  await _this.handleRowClickToRowSelect({ event, record });
                },
              };
            }}
          />
        </ConfigProvider>
      </div>
    );
  };
}
