/**
 * 在 form 表单中有标题的 Checkbox 组件
 */
import { Checkbox as AntdCheckbox } from "antd";
import React, { useEffect } from "react";
import { CheckboxChangeEvent } from "antd/lib/checkbox";
import { Note } from "./NoteComp";

// const CheckboxGroup = AntdCheckbox.Group;
/**
 * props
 */
export interface CheckboxProps {
  /** 支持全选 */
  allowAllSelected?: boolean;
  /** 禁用的数据节点 */
  disabledOptions?: string[];
  /** 整组失效 */
  disabled?: boolean;
  labelColor?: string;
  /** 备注信息 */
  note?: string;
  options?: { [key: string]: string };
  /** 指定选中的选项 */
  realVal?: string[];
  /** 选中选项对应展示数据 */
  showVal?: string[];
  title?: string;
  onChange?: (param: {
    realVal: string[];
    showVal: string[];
    event: CheckboxChangeEvent;
  }) => void;
  onGetOptions?: () => void;
  onClick?: (param: {
    realVal: string[];
    showVal: string[];
    event: React.MouseEvent;
  }) => void;
  onMouseEnter?: (param: {
    realVal: string[];
    showVal: string[];
    event: React.MouseEvent;
  }) => void;
  onMouseLeave?: (param: {
    realVal: string[];
    showVal: string[];
    event: React.MouseEvent;
  }) => void;
  onKeyDown?: (param: {
    realVal: string[];
    showVal: string[];
    event: React.KeyboardEvent;
  }) => void;
  onKeyPress?: (param: {
    realVal: string[];
    showVal: string[];
    event: React.KeyboardEvent;
  }) => void;
}
export const Checkbox = (props: CheckboxProps) => {
  const {
    allowAllSelected = false,
    disabledOptions = [],
    labelColor = "#000",
    options = {},
    onGetOptions,
    realVal = [],
    showVal = [],
    disabled = false,
    note,
    title,
    onChange,
    onClick,
    onMouseEnter,
    onMouseLeave,
    onKeyDown,
    onKeyPress,
  } = props;
  const plusAllSelectedRealVal = () => {
    const rest = Object.keys(options).filter((item) => !realVal.includes(item));
    return [...realVal, ...rest];
  };
  const plusAllSelectedShowVal = () => {
    const rest = Object.values(options).filter(
      (item) => !showVal.includes(item)
    );
    return [...showVal, ...rest];
  };
  const minusAllSelectedRealVal = () => {
    return realVal.filter((item) => !Object.keys(options).includes(item));
  };
  const minusAllSelectedShowVal = () => {
    return showVal.filter((item) => !Object.values(options).includes(item));
  };
  useEffect(() => {
    if (!Object.keys(options).length && onGetOptions) {
      onGetOptions();
    }
  }, []);
  return (
    <div>
      {title ? (
        <div
          className="leading-10"
          style={{
            color: labelColor,
          }}
        >
          {title}
          <Note title={note} />
        </div>
      ) : null}
      <div>
        {allowAllSelected ? (
          <AntdCheckbox
            disabled={disabled}
            indeterminate={
              Object.keys(options).some((item) => realVal.includes(item)) &&
              !Object.keys(options).every((item) => realVal.includes(item))
            }
            onChange={(event) => {
              const { checked } = event.target;
              onChange?.({
                realVal: checked
                  ? plusAllSelectedRealVal()
                  : minusAllSelectedRealVal(),
                showVal: checked
                  ? plusAllSelectedShowVal()
                  : minusAllSelectedShowVal(),
                event,
              });
            }}
            checked={Object.keys(options).every((item) =>
              realVal.includes(item)
            )}
          >
            全选
          </AntdCheckbox>
        ) : null}
        {Object.keys(options)?.map((key) => (
          <AntdCheckbox
            key={key}
            checked={realVal?.includes(key)}
            disabled={disabled || disabledOptions.includes(key)}
            onChange={(event) => {
              const { checked } = event.target;
              onChange?.({
                realVal: checked
                  ? [...realVal, key]
                  : realVal.filter((item) => item !== key),
                showVal: checked
                  ? [...showVal, options[key]]
                  : showVal.filter((item) => item !== options[key]),
                event,
              });
            }}
            onClick={(event) => {
              onClick?.({
                event,
                realVal,
                showVal,
              });
            }}
            onMouseEnter={(event) => {
              onMouseEnter?.({
                event,
                realVal,
                showVal,
              });
            }}
            onMouseLeave={(event) => {
              onMouseLeave?.({
                event,
                realVal,
                showVal,
              });
            }}
            onKeyDown={(event) => {
              onKeyDown?.({
                event,
                realVal,
                showVal,
              });
            }}
            onKeyPress={(event) => {
              onKeyPress?.({
                event,
                realVal,
                showVal,
              });
            }}
          >
            {options[key]}
          </AntdCheckbox>
        ))}
      </div>
    </div>
  );
};
