/**
 * 在 form 表单中有标题的 Input 组件
 */
import React, { useState } from "react";
import { AutoComplete } from "antd";

/**
 * FormInput 必须的 props
 */
export interface HYAutoCompleteProps {
  /** 自动获取焦点 */
  autoFocus?: boolean;
  /** 后置标签 */
  /** 是否允许清除内容 */
  allowClear?: boolean;
  /** 是否要边框 */

  disabled?: boolean;
  /** 标题颜色 */
  labelColor?: string;
  /** 备注信息 */
  note?: string;
  /** 校验失败的提示信息 */
  noteForError?: string;
  /** 提示信息 */
  placeholder?: string;
  /** 前缀图标 */
  prefix?: React.ReactNode;
  /** 实际值 */
  realVal?: string;
  /** 显示值 */
  showVal?: string;
  /** 尺寸 large: 40px/middle: 32px/small: 24px */
  size?: "large" | "middle" | "small";
  /** 后缀 */
  suffix?: React.ReactNode;
  /** 标题 */
  title?: string;
  /** 值改变 */
  onChange?: (param: { realVal: string }) => void;
  /** 失焦 */
}

export const HYAutoComplete: React.FC<HYAutoCompleteProps> = (props) => {
  const { labelColor = "#A4A4A4", realVal, title, onChange } = props;
  return (
    <div>
      <div
        className="leading-10"
        style={{
          color: labelColor,
        }}
      >
        {title}
      </div>
      <AutoComplete
        value={realVal}
        onChange={(data) => {
          if (typeof onChange !== "function") return;
          onChange({ realVal: data });
        }}
      />
    </div>
  );
};
