import React from "react";

export type BlurAwayProps = {
  onBlur?: (event) => void;
  children?: React.ReactNode;
};
export const BlurAway = (props: BlurAwayProps) => {
  return (
    <span
      onBlur={(e) => {
        props.onBlur?.(e);
      }}
    >
      {props.children}
    </span>
  );
};
