import {
  Tag,
  Table,
  Input,
  message as AntdMessage,
  ConfigProvider,
} from "antd";
import React, { CSSProperties } from "react";
import { HYModal } from "./HYModal";
import zhCN from "antd/es/locale/zh_CN";
export type SelectedTag = { key: string; text: string };
export type SelectedTagsProps = {
  onDelete: (param: SelectedTag) => void;
  selectedTags: SelectedTag[];
  onClearAll: () => void;
};
export interface FormButtonProps {
  /** 将按钮宽度调整为其父宽度的选项 */
  block?: boolean;
  /** 失效 */
  disabled?: boolean;
  /** 背景透明 */
  ghost?: boolean;
  /** 图标 */
  icon?: React.ReactNode;
  /** 按钮形状 */
  shape?: "circle" | "round";
  /** 尺寸： */
  size?: "small" | "large" | "middle";
  /** 按钮类型 */
  type?: "primary" | "ghost" | "dashed" | "link" | "text" | "default";
  /** 按钮内部的显示内容 */
  title: React.ReactNode;
  /** 备注信息 */
  note?: string;
  /** 单击 */
  onClick?: (param: { event: React.MouseEvent }) => void;
  /** 双击 */
  onDbClick?: (param: { event: Event }) => void;
  /** 鼠标移入 */
  onMouseIn?: (param: { event: Event }) => void;
  /** 鼠标移出 */
  onMouseOut?: (param: { event: Event }) => void;
}
export interface ModalProps {
  /** Modal 完全关闭后的回调 */
  afterClose?: () => void;
  /** Modal body 样式 */
  bodyStyle?: CSSProperties;
  /** cancel 按钮 props */
  cancelButtonProps?: FormButtonProps;
  /** 取消按钮文字 */
  cancelText?: React.ReactNode;
  /** 垂直居中展示 Modal */
  centered?: boolean;
  /** 是否显示右上角的关闭按钮 */
  closable?: boolean;
  /** 自定义关闭图标 */
  closeIcon?: React.ReactNode;
  /** 确定按钮 loading */
  confirmLoading?: boolean;
  /** 关闭时销毁 Modal 里的子元素 */
  destroyOnClose?: boolean;
  /** 对话框关闭后是否需要聚焦触发元素 */
  focusTriggerAfterClose?: boolean;
  /** 底部内容 */
  footer?: React.ReactNode;
  /** 强制渲染 Modal */
  forceRender?: boolean;
  /** 指定 Modal 挂载的 HTML 节点, false 为挂载在当前 dom */
  getContainer?: HTMLElement | (() => HTMLElement) | false;
  /** 是否支持键盘 esc 关闭 */
  keyboard?: boolean;
  /** 是否展示遮罩 */
  mask?: boolean;
  /** 点击蒙层是否允许关闭 */
  maskClosable?: boolean;
  /** 遮罩样式 */
  maskStyle?: CSSProperties;
  /** 自定义渲染对话框 */
  modalRender?: (node: React.ReactNode) => React.ReactNode;
  /** ok 按钮 props */
  okButtonProps?: FormButtonProps;
  /** 确认按钮文字 */
  okText?: React.ReactNode;
  /** 确认按钮类型 */
  okType?: string;
  /** 可用于设置浮层的样式，调整浮层位置等 */
  style?: CSSProperties;
  /** 标题 */
  title?: string;
  /** 对话框是否可见 */
  visible?: boolean;
  /** 宽度 */
  width?: string | number;
  /** 对话框外层容器的类名 */
  wrapClassName?: string;
  /** 设置 Modal 的 z-index */
  zIndex?: number;
  /** 子元素 */
  children: React.ReactNode;
  onCancel?: () => void;
  onOk?: (value) => void;
}
export interface TableProps {
  dataSource: { [key: string]: unknown }[];
  rowKey: string;
  columns: { dataIndex: string; title: string }[];
  onRequest: (
    pagination: { current: number; pageSize: number },
    filter: { [key: string]: unknown }
  ) => void;
  tagColumn: string;
  rowSelection: {
    type: "radio" | "checkbox";
    count?: number;
  };
  selectedRows?: { [key: string]: unknown }[];
  pagination?: {
    total?: number;
    current?: number;
    pageSize?: number;
  };
}
export type DataSelectorAsTableProps = {
  modal: ModalProps;
  table: TableProps;
};
/** 带关键字搜索的表格 */
export class TableWithSearch extends React.Component {
  state = {
    searchValue: "",
  };
  getRowSelection = () => {
    const { rowSelection, selectedRows, rowKey, onSelect } = this.props;
    const type = rowSelection?.type || "radio";
    return {
      ...(rowSelection || {}),
      type,
      selectedRowKeys: selectedRows?.map((item) => item?.[rowKey]),
      onSelect: (record, selected) => {
        this.handleSelect({ record, selected });
      },
      onSelectAll: (selected, _selectedRows, changeRows) => {
        if (selected) {
          onSelect?.([...changeRows, ...selectedRows]);
        } else {
          const changeRowKeys = changeRows.map((item) => item?.[rowKey]);
          onSelect?.(
            selectedRows.filter(
              (item) => !changeRowKeys.includes(item?.[rowKey])
            )
          );
        }
      },
    };
  };
  getPagination = () => {
    const { pagination, dataSource } = this.props;
    return {
      pageSizeOptions: ["10", "20", "30", "40", "50", "100"],
      showQuickJumper: true,
      showSizeChanger: true,
      total: pagination?.total || dataSource?.length || 0,
      size: "small",
      current: 1,
      pageSize: 10,
      showTotal: (total, range) => {
        return `总共 ${total} 条`;
      },
      ...pagination,
    };
  };
  handleSelect = ({ record, selected }) => {
    const { selectedRows, rowKey, onSelect, rowSelection } = this.props;
    const { type } = rowSelection || {};
    let _selectedRows;
    if (!type || type === "radio") {
      _selectedRows = [record];
    } else {
      const recordKey = record?.[rowKey];
      if (
        (selected &&
          recordKey &&
          selectedRows?.find((item) => item?.[rowKey] === recordKey)) ||
        !selected
      ) {
        _selectedRows = (selectedRows || []).filter(
          (item) => item?.[rowKey] !== record?.[rowKey]
        );
      } else {
        _selectedRows = [record, ...(selectedRows || [])];
      }
    }
    onSelect?.(_selectedRows);
  };
  getFilter = () => {
    const { searchValue } = this.state;
    const { columns } = this.props;
    if (!searchValue) return {};
    const filter = {};
    columns.forEach((item) => {
      filter[item.dataIndex] = searchValue;
    });
    return filter;
  };
  render() {
    const { searchValue } = this.state;
    const { dataSource, columns, rowKey, pagination, onRequest } = this.props;
    return (
      <div>
        <Input.Search
          allowClear
          placeholder="请输入关键字"
          onSearch={(keyword) => {
            this.setState(
              {
                searchValue: keyword,
              },
              () => {
                const filter = this.getFilter();
                onRequest?.(this.getPagination(), filter);
              }
            );
          }}
        />
        <ConfigProvider locale={zhCN}>
          <Table
            dataSource={dataSource || []}
            columns={columns || []}
            rowKey={rowKey || "id"}
            rowSelection={this.getRowSelection()}
            onRow={(record) => {
              const _this = this;
              return {
                onClick: () => {
                  _this.handleSelect({ record, selected: true });
                },
                onDoubleClick: () => {
                  _this.handleSelect({ record, selected: true });
                },
              };
            }}
            onChange={(pagination) => {
              onRequest(pagination, this.getFilter());
            }}
            pagination={this.getPagination()}
          />
        </ConfigProvider>
      </div>
    );
  }
}
/**
 * 底部栏
 * 支持数据清空，支持清除个别数据
 */

export const SelectedTags = ({
  onClearAll,
  onDelete,
  selectedTags,
}: SelectedTagsProps) => {
  return (
    <div
      className="border border-gray-400 border-solid pl-2 pt-2 overflow-auto"
      style={{ height: 69 }}
    >
      {selectedTags?.length > 0 ? (
        <Tag
          className="cursor-pointer"
          color="#488CF0"
          style={{ marginBottom: 8 }}
          onClick={(e) => {
            onClearAll?.();
          }}
        >
          全部清空
        </Tag>
      ) : null}
      {selectedTags.map(({ key, text }) => (
        <Tag
          onClose={(e) => {
            onDelete({ key, text });
          }}
          closable
          key={key}
        >
          {text}
        </Tag>
      ))}
    </div>
  );
};
/**
 * 弹窗选择-表格形式
 */

export class DataSelectorAsTable extends React.Component<DataSelectorAsTableProps> {
  constructor(props) {
    super(props);
    this.state = {
      selectedRows: this.sliceSelectedRowsByCount(
        this.props.table?.selectedRows
      ),
    };
  }

  componentDidMount() {
    this.setState({
      selectedRows: this.props.table?.selectedRows || [],
    });
  }
  getRowKeysString(selectedRows) {
    const { rowKey } = this.props.table || {};
    return selectedRows?.map((row) => row?.[rowKey] || "").join(",");
  }
  componentDidUpdate(prevProps) {
    const rowKeyStringInPP = this.getRowKeysString(
      prevProps.table?.selectedRows
    );
    const rowKeyStringInCP = this.getRowKeysString(
      this.props.table?.selectedRows
    );
    const rowKeyStringInCS = this.getRowKeysString(this.state.selectedRows);
    if (
      rowKeyStringInPP !== rowKeyStringInCP &&
      rowKeyStringInCS !== rowKeyStringInCP
    ) {
      this.setState({
        selectedRows: this.sliceSelectedRowsByCount(
          this.props.table?.selectedRows
        ),
      });
    }
  }
  sliceSelectedRowsByCount = (selectedRows) => {
    const { count, type } = this.props.table?.rowSelection || {};
    if (!type || type === "radio" || count === undefined)
      return selectedRows || [];
    return selectedRows?.slice(0, count) || [];
  };
  render() {
    const { table, modal } = this.props;
    const { selectedRows } = this.state;
    const { tagColumn, onRequest, dataSource, columns, rowKey, rowSelection } =
      table || {};
    return (
      <HYModal
        {...(modal || {})}
        destroyOnClose={true}
        onCancel={() => {
          modal?.onCancel?.();
          this.setState({
            selectedRows: this.sliceSelectedRowsByCount(table?.selectedRows),
          });
        }}
        onOk={() => {
          modal?.onOk?.(selectedRows);
          this.setState({ selectedRows: [] });
        }}
      >
        <TableWithSearch
          dataSource={dataSource || []}
          columns={columns || []}
          rowKey={rowKey || "id"}
          rowSelection={rowSelection}
          selectedRows={selectedRows}
          onRequest={(pagination, filter) => {
            onRequest?.(pagination, filter);
          }}
          onSelect={(selectedRowsParam) => {
            const { count, type } = rowSelection || {};
            if (
              count !== undefined &&
              type === "checkbox" &&
              selectedRowsParam?.length > count
            ) {
              AntdMessage.error(
                `当前操作将使数据选中行数超过最大选中数据量（${count}条）`
              );
            } else {
              this.setState({
                selectedRows: selectedRowsParam,
              });
            }
          }}
        />
        <SelectedTags
          selectedTags={selectedRows?.map((item) => ({
            key: item?.[rowKey],
            text: item?.[tagColumn] || "",
          }))}
          onClearAll={() => {
            this.setState({ selectedRows: [] });
          }}
          onDelete={({ key }) => {
            this.setState({
              selectedRows: selectedRows.filter(
                (item, _i) => item?.[rowKey] !== key
              ),
            });
          }}
        />
      </HYModal>
    );
  }
}
