/**
 * 在 form 表单中有标题的 Input 组件
 */
import React, { useState } from "react";
import { Drawer } from "antd";
/**
 * FormInput 必须的 props
 */
export interface HYDrawerProps {
  
  visible?: boolean;
  /** 值改变 */
  onClose?: () => void;
  /** 失焦 */ 
}
 

export const HYDrawer: React.FC<HYDrawerProps> = (props) => {
  const {
    onClose,
    visible
   
  } = props;
  const [isFocus, setState] = useState(false);
  const [internalValue, setInternalValue] = useState("");
  return (
    <div>
 
        <Drawer
              title="Basic Drawer"
              placement="right"
              closable={false}
              onClose={onClose}
              visible={visible}
        />
    </div>
  );
};
