/**
 * 在 form 表单中有标题的 Input 组件
 */
import React, { useState } from "react";
import { Cascader } from "antd";
/**
 * FormInput 必须的 props
 */
export interface HYCascaderProps {
  
  options?: Option[];
  /** 值改变 */
  onChange?: (param: { realVal: any; selectedOptions?: any }) => void;
  /** 失焦 */ 
}

interface Option {
  value: string | number;
  label?: React.ReactNode;  
  disabled?: boolean;
  children?: Option[];
}

export const HYCascader: React.FC<HYCascaderProps> = (props) => {
  const {
    options,
    onChange
   
  } = props;
  const [isFocus, setState] = useState(false);
  const [internalValue, setInternalValue] = useState("");
  return (
    <div>
 
        <Cascader
          options={options}
          onChange={(value, selectedOptions) => {
            if (typeof onChange !== "function") return;
            onChange({ realVal: value, selectedOptions });
          }} 
        />
    </div>
  );
};
