/**
 * 在 评分 组件
 */
import { Rate as AntdRate } from "antd";
import React, { CSSProperties } from "react";
import { Note } from "./NoteComp";

export interface RateCompProps {
  /** 是否允许再次点击后清除 */
  allowClear?: boolean;
  /** 是否允许半选 */
  allowHalf?: boolean;
  /** 自动获取焦点（当前版本antd暂不提供支持） */
  // autoFocus?: boolean;
  /** 自定义字符 */
  character?: React.ReactNode;
  /** 自定义类名 */
  className?: string;
  /** star 总数 */
  count?: number;
  /** 默认值 */
  defaultValue?: number;
  /** 只展示，不交互 */
  disabled?: boolean;
  /** 标题颜色 */
  labelColor?: string;
  /** 备注信息 */
  note?: string;
  /** 自定义样式对象 */
  style?: CSSProperties;
  /** 当前数，受控值 */
  realVal?: number;
  /** 标题 */
  title?: string;
  /** 自定义每项的提示信息 */
  tooltips?: string[];
  /** （当前版本antd暂不提供支持） */
  // onBlur?: (param: {
  //   event: React.FocusEvent;
  //   realVal: number;
  //   showVal: string;
  // }) => void;
  onChange?: (param: { realVal: number; showVal: string }) => void;
  /** （当前版本antd暂不提供支持） */
  // onFocus?: (param: {
  //   event: React.FocusEvent;
  //   realVal: number;
  //   showVal: string;
  // }) => void;
  onHoverChange?: (param: { realVal: number; showVal: string }) => void;
  /** （当前版本antd暂不提供支持） */
  // onKeyDown?: (param: {
  //   event: React.KeyboardEvent;
  //   realVal: number;
  //   showVal: string;
  // }) => void;
}

export const Rate: React.FC<RateCompProps> = (props) => {
  const {
    allowClear = false,
    allowHalf = false,
    character,
    className,
    count,
    defaultValue,
    disabled = false,
    labelColor = "#000",
    note,
    style,
    realVal,
    title,
    tooltips,
    // onBlur,
    onChange,
    // onFocus,
    onHoverChange,
    // onKeyDown,
  } = props;

  return (
    <div>
      {title != undefined ? (
        <div
          className="leading-10"
          style={{
            color: labelColor,
          }}
        >
          {title}
          <Note title={note} />
        </div>
      ) : null}
      <AntdRate
        allowClear={allowClear}
        allowHalf={allowHalf}
        character={character}
        className={className}
        count={count}
        defaultValue={defaultValue}
        disabled={disabled}
        style={style}
        tooltips={tooltips}
        value={realVal}
        onChange={(value) => {
          onChange?.({
            realVal: value,
            showVal: tooltips?.[Math.floor(value)] || "",
          });
        }}
        onHoverChange={(value) => {
          onHoverChange?.({
            realVal: value,
            showVal: tooltips?.[Math.floor(value)] || "",
          });
        }}
      />
    </div>
  );
};
