/**
 * 对 getState 的进一步优化，当数据为 {arr: [{a: 1}, {a:2}]}时，调用 getState(state, 'arr.a') 得到 [1,2]
 * @param object 
 * @param path 
 * @param defaultValue 
 * @returns 
 */
export const deepGet = (object, path, defaultValue?) => {
  const pathArray = !Array.isArray(path)
  ? path.replace(/\[/g, ".").replace(/\]/g, "").split(".")
  : path;
  const data = pathArray.reduce((o, k) => {
    if(Array.isArray(o) && !(k in o)){
      return getDataInArray(o, k)
    }
    return (o || {})[k]
  }, object);
  if (data !== undefined || defaultValue === undefined) {
    return data;
  }
  return defaultValue;
}

const getDataInArray = (array, key)=>{
  return Array.isArray(array) && array.map(item=>{
    return deepGet(item, key);
  }) || []
}