import { Radio as AntdRadio } from "antd";
import React, { useEffect } from "react";
import { RadioChangeEvent } from "antd/lib/radio";
import { Note } from "./NoteComp";

export interface RadioProps {
  /** RadioButton 的风格样式，目前有描边和填色两种风格 */
  buttonStyle?: "outline" | "solid";
  /** 禁选 */
  disabled?: boolean;
  /** 禁选的选项 */
  disabledOptions?: string[];
  /** 标题颜色 */
  labelColor?: string;
  /** 备注信息 */
  note?: string;
  /** 选项 */
  options?: { [key: string]: string };
  /** 用于设置 Radio options 类型 */
  optionType?: "default" | "button";
  /** 大小，只对按钮样式生效 */
  size?: "large" | "middle" | "small";
  /** 实际值 */
  realVal?: string;
  /** 显示值 */
  showVal?: string;
  /** 标题 */
  title?: string;
  onChange?: (param: {
    event: RadioChangeEvent;
    realVal: string;
    showVal: string;
  }) => void;
  onGetOptions?: () => void;
  onMouseEnter?: (param: {
    event: React.MouseEvent;
    realVal?: string;
    showVal?: string;
  }) => void;
  onMouseLeave?: (param: {
    event: React.MouseEvent;
    realVal?: string;
    showVal?: string;
  }) => void;
}
export const Radio = (props: RadioProps) => {
  const {
    buttonStyle = "outline",
    disabled = false,
    disabledOptions = [],
    labelColor = "#000",
    note,
    options = {},
    onGetOptions,
    optionType = "default",
    showVal,
    realVal,
    size = "middle",
    title,
    onChange,
    onMouseEnter,
    onMouseLeave,
  } = props;

  useEffect(() => {
    if (!Object.keys(options).length && onGetOptions) {
      onGetOptions();
    }
  }, []);
  return (
    <>
      {title ? (
        <div
          className="leading-10"
          style={{
            color: labelColor,
          }}
        >
          {title}
          <Note title={note} />
        </div>
      ) : null}
      <AntdRadio.Group
        buttonStyle={buttonStyle}
        disabled={disabled}
        optionType={optionType}
        size={size}
        value={realVal}
        onChange={(event) => {
          const { value } = event.target;
          onChange?.({
            event,
            realVal: value,
            showVal: options[value],
          });
        }}
        onMouseEnter={(event) => {
          onMouseEnter?.({
            event,
            realVal,
            showVal,
          });
        }}
        onMouseLeave={(event) => {
          onMouseLeave?.({
            event,
            realVal,
            showVal,
          });
        }}
        options={Object.keys(options)?.map((key) => ({
          key,
          value: key,
          label: options[key],
          disabled: disabled || disabledOptions.includes(key),
        }))}
      ></AntdRadio.Group>
    </>
  );
};
