/**
 * 在 form 表单中有标题的 Input 组件
 */
import { Select } from "antd";
import React, { useEffect } from "react";
import { Note } from "./NoteComp";
/**
 * DropdownSelector 必须的 props
 */
export type BasicRealVal = string | number;
export interface DropdownSelectorProps {
  /** 默认获取焦点 */
  autoFocus?: boolean;
  /** 是否允许清空 */
  allowClear?: boolean;
  /** 是否有边框 */
  bordered?: boolean;
  /** 自定义清除图标 */
  clearIcon?: React.ReactNode;
  /** 禁用 */
  disabled?: boolean;
  /** 下拉菜单的 className 属性 */
  dropdownClassName?: string;
  /** 下拉菜单和选择器同宽 */
  dropdownMatchSelectWidth?: boolean | number;
  /** 设置弹窗滚动高度 */
  listHeight?: number;
  /** 加载中状态 */
  loading?: boolean;
  /** 最多显示多少个 */
  maxTagCount?: number;
  /** 最大显示的 tag 文本长度 */
  maxTagTextLength?: number;
  /** 模式（单选/多选） */
  mode?: "single" | "multi";
  /** 当下拉列表为空时显示的内容 */
  notFoundContent?: React.ReactNode;
  /** 备注信息 */
  note?: string;
  /** 提示信息 */
  placeholder?: string;
  /** 标题 */
  title: string;
  /** 标题颜色 */
  labelColor?: string;
  /** 实际值 */
  realVal: BasicRealVal | BasicRealVal[];
  /** 自定义的多选框清除图标 */
  removeIcon?: React.ReactNode;
  /** 显示值 */
  showVal?: string;
  /** 是否允许搜索 */
  showSearch?: boolean;
  /** 尺寸 */
  size?: "large" | "middle" | "small";
  /** 后缀 */
  suffixIcon?: React.ReactNode;
  /** 校验失败的提示信息 */
  noteForError?: string;
  /** 数据项 */
  options?: { [key: string]: string };
  onGetOptions?: () => void;
  /** 值改变 */
  onChange?: (param: {
    realVal: BasicRealVal | BasicRealVal[];
    showVal: BasicRealVal | BasicRealVal[];
    option;
  }) => void;
  /** 搜索 */
  searchValue?: string;
  onSearch?: (param: { searchValue: string }) => void;

  /** 失焦 */
  onBlur?: (param: {
    event: React.FocusEvent;
    realVal: BasicRealVal | BasicRealVal[];
    showVal?: BasicRealVal | BasicRealVal[];
  }) => void;
  /** 清除内容 */
  onClear?: () => void;
  /** 聚焦 */
  onFocus?: (param: {
    event: React.FocusEvent;
    realVal: BasicRealVal | BasicRealVal[];
    showVal?: BasicRealVal | BasicRealVal[];
  }) => void;
  // /** 鼠标移入 */
  onMouseEnter?: (param: {
    event: React.MouseEvent;
    realVal: BasicRealVal | BasicRealVal[];
    showVal?: BasicRealVal | BasicRealVal[];
  }) => void;
  // /** 鼠标移出 */
  onMouseLeave?: (param: {
    event: React.MouseEvent;
    realVal: BasicRealVal | BasicRealVal[];
    showVal?: BasicRealVal | BasicRealVal[];
  }) => void;
  // /** 双击 */
  // onDbClick?: (param: {
  //   event: Event;
  //   realVal: BasicRealVal | BasicRealVal[];
  //   showVal: string;
  // }) => void;
  /** 展开下拉菜单的回调 */
  onDropdownVisibleChange?: (open: boolean) => void;
}

export const DropdownSelector = (props: DropdownSelectorProps) => {
  const {
    autoFocus,
    allowClear,
    bordered,
    clearIcon,
    disabled = false,
    dropdownClassName,
    dropdownMatchSelectWidth,
    listHeight = 256,
    loading = false,
    maxTagCount,
    maxTagTextLength,
    notFoundContent,
    note,
    removeIcon,
    size = "middle",
    suffixIcon,
    title,
    realVal,
    showVal,
    labelColor,
    options,
    onGetOptions,
    onChange,
    placeholder,
    mode,
    showSearch,
    searchValue,
    onBlur,
    onClear,
    onFocus,
    onDropdownVisibleChange,
    onMouseEnter,
    onMouseLeave,
    onSearch,
  } = props;
  const getShowValByRealVal = (value) => {
    if (Array.isArray(value)) {
      return value.map((item) => options?.[item] || "");
    }
    return options?.[value] || "";
  };
  useEffect(() => {
    if (!options && onGetOptions) {
      onGetOptions();
    }
  }, []);

  return (
    <>
      <div
        className="leading-10"
        style={{
          color: labelColor,
        }}
      >
        {title}
        <Note title={note} />
      </div>
      <Select
        style={{ width: 300 }}
        autoFocus={autoFocus || false}
        allowClear={allowClear || false}
        bordered={"bordered" in props ? bordered : true}
        clearIcon={clearIcon}
        disabled={disabled}
        dropdownClassName={dropdownClassName}
        dropdownMatchSelectWidth={
          "dropdownMatchSelectWidth" in props ? dropdownMatchSelectWidth : true
        }
        listHeight={listHeight}
        loading={loading}
        maxTagCount={maxTagCount}
        maxTagTextLength={maxTagTextLength}
        removeIcon={removeIcon}
        notFoundContent={notFoundContent}
        showSearch={showSearch || false}
        size={size}
        suffixIcon={suffixIcon}
        mode={mode === "multi" ? "multiple" : undefined}
        placeholder={placeholder}
        value={realVal}
        onChange={(value, option) => {
          if (typeof onChange !== "function") return;
          const showValue = getShowValByRealVal(value);
          onChange({
            realVal: value,
            showVal: showValue,
            option,
          });
        }}
        onBlur={(event) => {
          if (typeof onBlur !== "function") return;
          onBlur({ realVal, showVal, event });
        }}
        onClear={() => {
          if (typeof onClear !== "function") return;
          onClear();
        }}
        onFocus={(event) => {
          if (typeof onFocus !== "function") return;
          onFocus({ realVal, showVal, event });
        }}
        onSearch={(newSearchValue) => {
          if (
            (!searchValue && !newSearchValue) ||
            searchValue === newSearchValue
          )
            return;
          if (typeof onSearch !== "function") return;
          onSearch({ searchValue: newSearchValue });
        }}
        onDropdownVisibleChange={(open) => {
          if (typeof onDropdownVisibleChange !== "function") return;
          onDropdownVisibleChange(open);
        }}
        onMouseEnter={(event) => {
          if (typeof onMouseEnter !== "function") return;
          onMouseEnter({ realVal, showVal, event });
        }}
        onMouseLeave={(event) => {
          if (typeof onMouseLeave !== "function") return;
          onMouseLeave({ realVal, showVal, event });
        }}
      >
        {Object.keys(options || {}).map((key) => (
          <Select.Option key={key} value={key}>
            {options?.[key]}
          </Select.Option>
        ))}
      </Select>
    </>
  );
};
