/**
 * 在 form 表单中有标题的 Input 组件
 */
import zhCN from "antd/es/date-picker/locale/zh_CN";
import "dayjs/locale/zh-cn";
// import moment from "moment";
import React from "react";
import dayjs, { Dayjs } from "dayjs";
import dayjsGenerateConfig from "rc-picker/lib/generate/dayjs";
import generatePicker from "antd/es/date-picker/generatePicker";
import { Note } from "./NoteComp";

const DatePicker = generatePicker<Dayjs>(dayjsGenerateConfig);

/**
 * FormInput 必须的 props
 */
export interface FormTimePickerCompProps {
  /** 是否显示清楚按钮 */
  allowClear?: boolean;
  /** 是否显示边框 */
  bordered?: boolean;
  /** 选择器 className */
  className?: string;
  /** 是否禁用 */
  disabled?: boolean;
  /** 额外的弹出日历 className */
  dropdownClassName?: string;
  format?: string;
  timeMode?: "year" | "month" | "week" | "date" | "time" | "fullTime";
  /** 控制弹层是否弹开 */
  open?: boolean;
  title: string;
  /** 标题颜色 */
  labelColor?: string;
  /** 提示信息 */
  placeholder?: string;
  /** 备注信息 */
  note?: string;
  /** 校验失败的备注信息 */
  noteForError?: string;
  /** 是否显示时间选择器 */
  showTime?: boolean;
  /** 实际值 */
  realVal;
  /** 时间类型：时间戳、字符串 */
  timeType: "string" | "stamp";
  /** 显示值 */
  showVal?: string;
  /** 大小 */
  size?: "large" | "small" | "middle";
  /** 值改变 */
  onChange?: (param: {
    realVal: string | null;
    moment;
    showVal: string | null;
  }) => void;
  /** 失焦 */
  onBlur?: (param: {
    event: React.FocusEvent;
    realVal?: string;
    showVal?: string;
  }) => void;
  /** 聚焦 */
  onFocus?: (param: {
    event: React.FocusEvent;
    realVal?: string;
    showVal?: string;
  }) => void;
}
/**
 * TODO:
 * 1.格式化；
 * 2.禁选日期时间范围；
 * 3.时间组；
 * 4.自定义单元格展示：
 */
/**
 * FOCUS:
 * inputReadonly 为只读（避免在移动设备上打开虚拟键盘）
 */
export const FormTimePicker: React.FC<FormTimePickerCompProps> = (props) => {
  const {
    allowClear,
    bordered,
    className,
    disabled,
    dropdownClassName,
    // format,
    note,
    open,
    timeMode = "date",
    title,
    labelColor,
    placeholder = "请选择",
    size,
    realVal,
    timeType,
    showTime,
    showVal,
    onBlur,
    onChange,
    onClick,
    // onDbClick,
    onFocus,
  } = props;
  const format = "YYYY-MM-DD HH:mm:ss";
  const getPicker = () => {
    if (timeMode === "fullTime") {
      return { showTime: true };
    } else {
      return { picker: timeMode };
    }
  };
  const getValue = () => {
    if (realVal) {
      return { value: dayjs(realVal) };
    }
    return {};
  };
  const handleChange = (moment) => {
    if (typeof onChange !== "function") return;
    if (!moment) {
      onChange({
        moment: null,
        realVal: null,
        showVal: null,
      });
      return;
    }
    onChange({
      moment: moment,
      realVal:
        timeType === "string" ? moment?.format(format) : moment?.valueOf(),
      showVal: moment?.format(format),
    });
  };
  const handleBlur = (event) => {
    if (typeof onBlur !== "function") return;
    onBlur({
      event,
      realVal,
      showVal,
    });
  };
  const handleFocus = (event) => {
    if (typeof onFocus !== "function") return;
    onFocus({
      event,
      realVal,
      showVal,
    });
  };
  return (
    <div>
      <div
        className="leading-10"
        style={{
          color: labelColor,
        }}
      >
        {title}
        <Note title={note} />
      </div>

      <DatePicker
        allowClear={"allowClear" in props ? allowClear : true}
        bordered={"bordered" in props ? true : bordered}
        inputReadOnly={true}
        className={className}
        disabled={disabled || false}
        dropdownClassName={dropdownClassName}
        locale={zhCN}
        open={open}
        placeholder={placeholder}
        size={size || "middle"}
        onFocus={handleFocus}
        onBlur={handleBlur}
        onOk={handleChange}
        onChange={handleChange}
        {...getPicker()}
        {...getValue()}
      />
    </div>
  );
};
