import React, { useEffect, useState } from "react";

interface FlexLayoutProps extends React.HTMLAttributes<HTMLDivElement> {}

export const PageContainer = React.forwardRef<any, FlexLayoutProps>(
  (props, ref) => {
    const {
      children,
      style,
      title,
      onPageLoad,
      onPageDestroy,
      onPageResize,
      ...other
    } = props;
    const [isLoad, setIsLoad] = useState(false);
    useEffect(() => {
      if (!isLoad) {
        onPageLoad && onPageLoad();
        setIsLoad(true);
      }
    }, [onPageLoad]);
    return (
      <div
        ref={ref}
        style={Object.assign({}, { minHeight: 200, padding: 20 }, style)}
        {...other}
      >
        {title && <h3>{title}</h3>}
        {children}
      </div>
    );
  }
);
